<template>
  <div class="main">
    <div class="addressForm">
      <el-form :inline="true" :model="formData" ref="formData">
        <el-form-item prop="address" :rules="rules.address">
          <el-input
            v-model="formData.address"
            placeholder="输入需要查询积分的地址"
            :disabled="tableLoading"
          ></el-input>
        </el-form-item>
        <el-button type="primary" @click="getTableData" :loading="tableLoading"
          >提交</el-button
        >
      </el-form>
    </div>
    <div class="Sum" v-show="isShow" v-loading="tableLoading">
      <el-card style="width: 100%; height: 100%" shadow="hover">
        <div class="Sum-card">
          <div class="left">
            <div class="Sum-card-title">$promise总燃烧数</div>
            <div>
              约 <span class="details-color">{{ promiseSum }}</span> T
            </div>
          </div>
          <div class="right">
            <div class="Sum-card-title">总积分数</div>
            <div>
              约 <span class="details-color">{{ pointsSum }}</span> 分
            </div>
          </div>
        </div>
      </el-card>
    </div>
    <div class="forTable" v-show="isShow" v-loading="tableLoading">
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        :default-sort="{ prop: 'date', order: 'descending' }"
        v-loading="tableLoading"
      >
        <el-table-column prop="date" label="日期" sortable align="center">
        </el-table-column>
        <el-table-column
          prop="promise"
          label="promise(T)"
          sortable
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="dailyPoints"
          sortable
          label="当日积分"
          align="center"
        >
        </el-table-column>
        <el-table-column
          sortable
          prop="points"
          label="当日总积分"
          align="center"
        >
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import http from "@/utils/requests";

export default {
  data() {
    return {
      formData: {
        address: "",
      },
      rules: {
        address: {
          required: true,
          message: "填入正确格式的地址",
          type: "string",
          trigger: "blur",
          pattern: /^0x[a-fA-F0-9]{40}$/,
        },
      },
      tableLoading: false,
      isShow: false,
      promiseSum: "****",
      pointsSum: "****",
      // tableData: [
      //   {
      //     date: "2016-05-02",
      //     name: "王小虎",
      //     address: "上海市普陀区金沙江路 1518 弄",
      //   },
      //   {
      //     date: "2016-05-04",
      //     name: "王小虎",
      //     address: "上海市普陀区金沙江路 1517 弄",
      //   },
      //   {
      //     date: "2016-05-01",
      //     name: "王小虎",
      //     address: "上海市普陀区金沙江路 1519 弄",
      //   },
      //   {
      //     date: "2016-05-03",
      //     name: "王小虎",
      //     address: "上海市普陀区金沙江路 1516 弄",
      //   },
      // ],
      tableData: [],
    };
  },
  methods: {
    async getTableData() {
      const isVaild = await this.test();
      if (isVaild) {
        this.isShow = true;
        this.tableLoading = true;
        this.tipNOTICE();
        const r = await http
          .post("/v1/calPoints", {
            address: this.formData.address,
          })
          .then((r) => {
            if (r.data.data.length == 0) {
              this.isShow = false;
              this.NOTICE("未查询到该地址的相关信息");
              return false;
            }
            this.promiseSum = r.data.data.promiseSum;
            this.pointsSum = r.data.data.pointsSum;
            this.tableData = r.data.data.details;
          })
          .catch((e) => {
            this.isShow = false;
            console.error(e);
            this.NOTICE(e);
          })
          .finally(() => {
            this.tableLoading = false;
          });
      } else {
        return false;
      }
    },
    NOTICE(msg) {
      this.$notify.error({
        title: "错误",
        message: msg,
        duration: 0,
      });
    },
    tipNOTICE() {
      this.$notify.info({
        title: "提示",
        message: "计算较为繁杂，需等待30-150秒左右",
        duration: 10000,
        // offset: 50,
      });
    },
    async test() {
      let isVaild = false;
      this.$refs.formData.validate((valid) => {
        if (valid) {
          isVaild = true;
        } else {
          isVaild = false;
        }
      });
      return isVaild;
    },
  },
};
</script>

<style lang="less" scoped>
.main {
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
  > div {
    margin-bottom: 20px;
  }
  .el-input {
    width: 400px;
  }
  .addressForm {
    width: 500px;
  }
  .Sum {
    width: 70%;
    height: 100px;
    .Sum-card {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      align-items: center;
      text-align: center;
      font-size: 20px;
      .Sum-card-title {
        margin-bottom: 10px;
        font-weight: 1000;
      }
    }
  }
  .forTable {
    width: 70%;
  }
  .details-color {
    color: rgb(0, 89, 255);
  }
}
@media (max-width: 500px) {
  .main {
    .addressForm {
      width: 100%;
      .el-form {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        > div {
          width: 100%;
        }
        .el-input {
          width: 100%;
        }
      }
    }
    .el-input {
      width: 100 vw;
    }
    .el-button {
      width: 90vw;
    }
  }
}
</style>