<template>
  <div class="main">
    <div class="cards">
      <el-card class="box-card" v-loading="cards.bnLoading" shadow="hover">
        <div slot="header" class="card-header">币安热钱包</div>
        <div class="card-content">
          <div>
            持币比例为
            <span class="details-red">{{ cards.bnBalancePercent }} </span>%
          </div>
          <div>
            打款收益为
            <span class="details-green">{{ cards.myRewardMultiplier }} </span>%
          </div>
        </div>
      </el-card>
      <el-card class="box-card" v-loading="cards.promiseLoading" shadow="hover">
        <div slot="header" class="card-header">燃烧约定</div>
        <div class="card-content">
          <div>
            已销毁 <span class="details-red">{{ cards.destroyPercent }} </span>%
          </div>
          <div>
            剩余
            <span class="details-green">{{ cards.remainDays }} </span>天
          </div>
        </div>
      </el-card>
    </div>
    <el-card class="title-card" shadow="hover">
      <div class="title-card-1">约定每日燃烧量</div>
      <div class="title-card-2">起始时间为当天与隔天的北京时间八点</div>
      <div class="title-card-2">每小时更新一次，最后更新于{{ updateTime }}</div>
    </el-card>
    <div class="line-bar" v-loading="chartloading" ref="chart"></div>
  </div>
</template>

<script>
// import * as echarts from "echarts";
import drawLine from "@/api/drawLine";
import getCardPromise from "@/api/getCardPromise";
import bnHotWalletReward from "@/api/bnHotWalletReward";
import echarts from "@/utils/echarts";

export default {
  name: "chart",
  data() {
    return {
      cards: {
        bnBalancePercent: "*",
        myRewardMultiplier: "*",
        remainDays: "*",
        destroyPercent: "*",
        promiseLoading: false,
        bnLoading: false,
      },
      updateTime: "*月*日*时*分*秒",
      date: [],
      promise: [],
      getPoints: [],
      isShowLabel: true,
      chartloading: false,

      // 拖动区开始点位
      startPer: 50,
    };
  },
  mounted() {
    this.bn();
    this.getCardPromise();
    this.getLineData();
  },
  methods: {
    async drawLine() {
      const myChart = echarts.init(this.$refs.chart);
      const options = {
        title: {
          show: false,
          subtext: "更新于" + this.updateTime,
          text: "约定每日燃烧量(UTC+8)",
          left: "middle",
          textAlign: "center",
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            crossStyle: {
              color: "#999",
            },
          },
        },
        grid: {
          top: 70,
          bottom: 70,
          left: 50,
          right: 50,
        },
        dataZoom: [
          {
            type: "slider",
            realtime: true,
            start: this.startPer,
            end: 100,
            xAxisIndex: 0,
          },
        ],
        toolbox: {
          feature: {
            restore: { show: true },
            saveAsImage: { show: true },
          },
        },
        legend: {
          left: "left",
          top: 10,
          data: ["当天燃烧数", "燃烧1T分到的积分"],
          orient: "vertical",
          tooltip: {
            show: true,
          },
        },
        xAxis: [
          {
            type: "category",
            data: this.date,
            axisLabel: {},
          },
        ],
        yAxis: [
          {
            type: "value",
            // name: "当天燃烧数",
            name: "",
            min: 0,
            max: 6000,
            interval: 3000,
            axisLabel: {
              formatter: "{value} T",
            },
            // splitLine: {
            //   show: false,
            // },
            // axisLabel: {
            //   show: false,
            // },
          },
          {
            type: "value",
            name: "",
            // name: "燃烧1T分到的积分",
            min: 0,
            max: 10000,
            interval: 5000,
            axisLabel: {
              formatter: "{value}",
            },
            // splitLine: {
            //   show: false,
            // },
            // axisLabel: {
            //   show: false,
            // },
          },
        ],
        series: [
          {
            name: "当天燃烧数",
            type: "bar",
            tooltip: {
              valueFormatter: function (value) {
                return value + " T";
              },
            },

            label: {
              show: false, // 显示标签
              position: "top", // 标签位置
              formatter: "{c}", // 标签内容格式，{c} 表示显示数据值
            },
            data: this.promise,
            // markPoint: {
            //   data: {
            //     yAxis: "10",
            //     x: "90%",
            //   },
            // },

            markLine: {
              silent: true,
              symbol: [null, null],
              precision: 0,
              label: {
                // show: false,
                color: "#5470c6",
              },
              data: [{ type: "average", name: "Avg" }],
            },
          },
          {
            name: "燃烧1T分到的积分",
            type: "line",
            yAxisIndex: 1,
            tooltip: {
              valueFormatter: function (value) {
                return value;
              },
            },
            itemStyle: {
              color: "#e51212f5", // 设置线的颜色为红色
            },
            data: this.getPoints,
            markLine: {
              silent: true,
              symbol: [null, null],
              precision: 0,
              label: {
                show: true,
                color: "#e51212f5",
              },
              data: [{ type: "average", name: "Avg" }],
            },
          },
        ],
      };
      myChart.setOption(options);
      myChart.dispatchAction({
        type: "showTip",
        seriesIndex: 0, // 如果有多个系列，可以指定系列索引
        dataIndex: this.date.length - 1, // 最后一条数据的索引
      });
    },
    async getLineData() {
      let timeoutTimes = 0;
      for (let i = 0; i < 5; i++) {
        try {
          this.chartloading = true;
          let response = await drawLine();
          this.date = response.data.date;
          this.promise = response.data.promise;
          this.getPoints = response.data.getPoints;
          this.updateTime = response.data.updateTime;
          if (this.date.length >= 10) {
            this.startPer = Math.round((1 - 10 / this.date.length) * 100);
          }

          this.drawLine();
          break;
        } catch (error) {
          console.error(error);
          timeoutTimes += 1;
        } finally {
          this.chartloading = false;
        }
      }
      if (timeoutTimes === 5) {
        this.undisappearNOTICE("图表数据请求超时，刷新页面");
      }
    },
    async getCardPromise() {
      let timeoutTimes = 0;
      for (let i = 0; i < 5; i++) {
        try {
          this.cards.promiseLoading = true;
          const r = await getCardPromise();
          this.cards.remainDays = r.data.remainDays;
          this.cards.destroyPercent = r.data.destroyPercent;
          break;
        } catch (error) {
          timeoutTimes += 1;
          console.error(error);
        } finally {
          this.cards.promiseLoading = false;
        }
      }
      if (timeoutTimes === 5) {
        this.undisappearNOTICE("卡片数据请求超时，刷新页面");
      }
    },
    async bn() {
      let timeoutTimes = 0;
      for (let i = 0; i < 5; i++) {
        try {
          this.cards.bnLoading = true;
          const r = await bnHotWalletReward();
          this.cards.bnBalancePercent = r.data.bnBalancePercent;
          this.cards.myRewardMultiplier = r.data.myRewardMultiplier;
          break;
        } catch (error) {
          timeoutTimes += 1;
          console.error(error);
        } finally {
          this.cards.bnLoading = false;
        }
      }
      if (timeoutTimes === 5) {
        this.undisappearNOTICE("卡片数据请求超时，刷新页面");
      }
    },
    NOTICE(msg) {
      this.$notify.error({
        title: "请求超时",
        message: msg,
        duration: 3000,
        // type: 'error'
      });
    },
    undisappearNOTICE(msg) {
      this.$notify.error({
        title: "请求超时",
        message: msg,
        duration: 0,
      });
    },
  },
};
</script>

<style lang="less" scoped>
.main {
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

  > div {
    margin-bottom: 20px;
  }
  .cards {
    width: 50%;
    display: flex;
    flex-direction: row;
    div:first-child {
      margin-right: 20px;
    }
    .box-card {
      width: 500px;

      .card-header {
        font-size: 30px;
        font-weight: 1000;
        text-align: center;
      }
      .card-content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        font-size: 25px;
        font-weight: 500;
      }
      .details-red {
        color: red;
      }
      .details-green {
        color: rgb(0, 133, 4);
      }
    }
  }
  .title-card {
    width: 70%;
    text-align: center;
    .title-card-1 {
      font-size: 25px;
      font-weight: 1000;
    }
    .title-card-2 {
      font-size: 15px;
      color: rgb(129, 129, 129);
    }
  }
  .line-bar {
    width: 70%;
    height: 500px;
  }
}
@media (max-width: 500px) {
  .main {
    .cards {
      width: 100%;
      display: flex;
      flex-direction: column;
      div:last-child {
        margin-bottom: 0px;
      }
      .box-card {
        width: 100%;
        margin-bottom: 20px;
      }
    }
    .title-card {
      width: 100%;
    }
    .line-bar {
      width: 100%;
      height: 500px;
    }
  }
}
</style>

